<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
    this.$nextTick(() => {
      console.log('')
      window.addEventListener('message', this.handleMessage)
    })
  },
  created () {
    // this.initData()
  },
  destroyed () {
    window.removeEventListener('message', this.handleMessage)
  },
  methods: {
    initData () {
      // 
      this.$store.commit('setRole', 1)
      // 
      const userInfo = {
        number: '001'
      }
      this.$store.commit('setUserInfo', userInfo)
      // 
      const companyInfo = {
        name: '',
        number: '100100100100101010',
        bank: '',
        openDate: '20120630',
        accountType: ''
      }
      this.$store.commit('setCompanyInfo', companyInfo)
    },
    sendMessage (cmd, params) {
      window.parent.postMessage({ cmd, params }, '*')
    },
    handleMessage (msg) {
      console.log('store')
      const { data } = msg
      if (data.cmd === 'preset') {
        // 
        this.$store.commit('setCompanyInfo', {
          name: data.params.company_title, // 
          number: data.params.tax_id, // 
          bank: data.params.bank_account_name // 
        })
        // 
        this.$store.commit('setUserInfo', {
          number: data.params.current_user_tel
        })
      }
      if (data.cmd === 'getAnswer') {
        const answer = this.$store.state.answer
        // answercreated_at
        // 
        if (answer.payTransferInfo && Array.isArray(answer.payTransferInfo) && answer.payTransferInfo.length > 0) {
          answer.payTransferInfo.forEach(v => {
            delete v.created_at
          })
        }
        // 
        if (answer.checkInfo && Object.keys(answer.checkInfo) > 0) {
          delete answer.checkInfo.created_at
        }
        // 
        if (answer.checkResultInfo && Array.isArray(answer.checkResultInfo) && answer.checkResultInfo.length > 0) {
          answer.checkResultInfo.forEach(v => {
            delete v.created_at
          })
        }
        const keys = Object.keys(answer)
        if (keys.length > 0) {
          this.sendMessage('echo', { business_id: data.params.business_id, answer: answer })
          this.$store.commit('resetAnswer')
        }
      }
    }
  }
}
</script>
